	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Spiegel Online Relaunch","date":"06 Sep 2006","slug":"/2006-09-06-spiegel-online-relaunch/","tags":"Y2006"},"html":"<p>Thanks to <a href=\"http://www.hebig.com/archives/003986.shtml\">Heiko for pointing out</a> that <a href=\"http://www.spiegel.de/netzwelt/netzkultur/0,1518,435448,00.html\">Spiegel Online has relaunched</a> (yet again). My first impression is... I like it. They've made a subtle change in the typography that makes it easier to read. For better or for worse, the <a href=\"http://www.spiegel.de/dienste/0,1518,419923,00.html\">SpOn sidebar in Firefox</a> is the first place I look for German news. Maybe Heiko can tell me if <a href=\"http://focus.msn.de/\">Focus</a> has a sidebar as well. Or <a href=\"http://www.zeit.de/\">Die Zeit</a>, <a href=\"http://couchblog.de/webpropaganda/\">Nico</a>?</p>"},"next":{"fields":{"title":"Poor Organization","date":"06 Sep 2006","slug":"/2006-09-06-poor-organization/"}},"previous":{"fields":{"title":"PapaScott Sells Out","date":"07 Sep 2006","slug":"/2006-09-07-papascott-sells-out/"}}},{"node":{"fields":{"title":"Poor Organization","date":"06 Sep 2006","slug":"/2006-09-06-poor-organization/","tags":"Y2006"},"html":"<p>How can you tell you've been in Germany too long? When you think the <a href=\"http://www.chillmost.com/2006/09/back-from-amsterdam.html\">Dutch are poorly organized</a>.</p>"},"next":{"fields":{"title":"More Einschulung","date":"03 Sep 2006","slug":"/2006-09-03-more-einschulung/"}},"previous":{"fields":{"title":"Spiegel Online Relaunch","date":"06 Sep 2006","slug":"/2006-09-06-spiegel-online-relaunch/"}}},{"node":{"fields":{"title":"More Einschulung","date":"03 Sep 2006","slug":"/2006-09-03-more-einschulung/","tags":"Y2006"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/232972161/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/94/232972161_446738bae9.jpg\" width=\"375\" height=\"500\" alt=\"IMG 1747\" /></a></p>\n<p>As I mentioned before, starting school is a big deal here in Germany, with ceremony, presents and family gathering on par with baptism, confirmation or graduation. There was even a church service before the official ceremony at the school, so here's Christopher at the church with his *Schultüte*.</p>"},"next":{"fields":{"title":"Einschulung","date":"02 Sep 2006","slug":"/2006-09-02-einschulung/"}},"previous":{"fields":{"title":"Poor Organization","date":"06 Sep 2006","slug":"/2006-09-06-poor-organization/"}}}],"pathPrefix":"","first":false,"last":false,"index":195,"pageCount":1101}}

